import React, { Component } from 'react';
import shortid from 'shortid';
import styles from './SignUpForm.module.css';


const Gender = {
  MALE: 'male',
  FEMALE: 'female'
}

export default class SignUpForm extends Component {
  state = {
    login: '',
    email: '',
    password: '',
    agreed: false,
    gender: null,
    age: ''
  };

  loginInputId = shortid.generate();
  emailInputId = shortid.generate();
  passwordlInputId = shortid.generate();
  checkedInputId = shortid.generate();

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.type === 'checkbox' ? e.target.checked : e.target.value });
  };

handleSubmit = e => {
  e.preventDefault();
  console.log('The link was clicked.');
this.props.onSignUp({...this.state});
this.reset();
};

reset = () => {
  this.setState({
    login: '',
      email: '',
      password: '',
      agreed: false,
      gender: null,
      age: ''
  })
};

  render() {
    const { login, email, password, agreed, gender, age } = this.state;

    return (

      // FORM - login, email, password
      <form className={styles.form} onSubmit={this.handleSubmit}>
        <label htmlFor={this.loginInputId}>
          Login
          <input
            type="text"
            value={login}
            name="login"
            onChange={this.handleChange}
            id={this.loginInputId}
          ></input>
        </label>
        <label htmlFor={this.emailInputId}>
          Email
          <input
            type="email"
            value={email}
            name="email"
            onChange={this.handleChange}
            id={this.emailInputId}
          ></input>
        </label>
        <label htmlFor={this.passwordlInputId}>
          Password
          <input
            type="password"
            value={password}
            name="password"
            onChange={this.handleChange}
            id={this.passwordlInputId}
          ></input>
        </label>

        {/* CHECKBOX */}
        <label htmlFor={this.checkedInputId}>
          Agree to terms
          <input type="checkbox"
          checked={agreed}
          onChange={this.handleChange}
          name='agreed'
          id={this.checkedInputId}
          />
        </label>

        {/* RADIO BUTTON */}
        <section>
          <h2 className={styles.h2}>Choose your gender:</h2>
          <label>
            Male
            <input
            type='radio'
            name='gender'
            checked={ gender === Gender.MALE }
            value={Gender.MALE}
            onChange={this.handleChange}
            />
          </label>
          <label>
            Female
            <input
            type='radio'
            name='gender'
            checked={ gender === Gender.FEMALE }
            value={Gender.FEMALE}
            onChange={this.handleChange}
            />
          </label>
        </section>

        {/* SELECT */}
        <label>
          Choose your age
        <select 
        name='age'
        value={age}
         onChange={this.handleChange}
         >
           <option value='' disabled>...</option>
           <option value='18-25'>18-25</option>
           <option value='26-35'>26-35</option>
           <option value='36+'>36+</option>
         </select>
        </label>
    
    {/* BUTTON SUBMIT */}
        <button type="submit" className={styles.button}>Sign up as {login}</button>
      </form>
    );
  }
}
